<template>
    <div class="singer-info-page">
        <div class="singer-info-wrap">
<!--            {{singermid}}-->
            <div class="singer-pic">
                <img :src="`http://y.gtimg.cn/music/photo_new/T001R300x300M000${singermid}.webp`" alt="">
            </div>
            <div class="singer-info-text" v-show="basicList">
                <div class="singer-name">{{singerName}}</div>
                <div class="singer-info">
                    <div class="singer-Introduction">
                        <div class="text-info">
                            {{basicInfo}}
                        </div>
                        <span class="more-op" @click.stop="clickMorepanel">[]</span>
                        <div class="more-panel" v-show="showMoreInfo" id="singer-more-info">
                            <div class="desc" >
                                {{ desc}}
                            </div>
                        </div>

                    </div>

                </div>
                <div class="song-info" >
                    <div class="single-num"><span><i>{{songTotal}}</i></span> </div>
                    <div class="album-num"><span> <i>{{albumTotal}}</i></span></div>
                    <div class="mv-num"><span>MV<i>{{mvTotal}}</i> </span></div>
                </div>
                <div class="play-op">
                    <div class="player-hot-song">
                        <span class="iconfont icon-bofang2"></span>
                        
                    </div>
                    <div class="attention">
                        <span class="iconfont icon-jia"></span>
                        {{followers}}
                    </div>
                </div>
            </div>
        </div>
<!--        <singer-info-show />-->
        <div class="rank-title"></div>
        <div class="rank-wrap">
            <ul class="content-nav-ul">
                <li v-for="(item,index) in contentNav" :key="index"
                    :class="`nav-li-${index}`"
                >
                    {{item}}
                </li>
            </ul>
            <div class="rank-info" >
                <ul v-for="(item,index) in hotSongs" :key="index" class="rank-info-ul" >
                    <li class="rank-info-li"
                        @mouseenter="OperateChange(index)"
                        @mouseleave="OperateChange">
                        <!---->
                        <span class="rank-value">
                            <i>{{index+1}}</i>
                        </span>
                        <span class="song-title-wrap"
                        >
                            <i class="song-title"><a href="javascript:;">{{item.title}}</a></i>
                            <i class="song-isonly iconfont icon-dujia" title="" v-show="item.isonly"></i>
                            <i class="song-ismv iconfont icon-MV" title="MV" v-show="item.ismv"></i>
                            <i class="song-isvip iconfont icon-VIP" title="vip" v-show="item.isvip"></i>
                            <!---->
                            <ul class="song-operating"
                                v-show="songOperatingShow==index"
                            >
                                <router-link :to="`/player/songmid=`+item.mid" tag="li">
                                    <i class="iconfont icon-bofang2">
                                    </i>
                                </router-link>
                          <!--      <li v-for="n in operatingList">
                                   <i :class="['iconfont',`icon-${n}`]">
                                   </i>
                                </li>-->
                            </ul>
                        </span>
                        <i class="rank-singername"><a href="javascript:;">{{item.album.title}}</a></i>
                        <i class="rank-time">{{songTime(item.interval)}}</i>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import SingerInfoShow from "@/components/main/singer/SingerInfoShow";
    import '@/assets/css/singerinfopage.scss'
    import api from "@/api";
    export default {
        props:{
            singermid:{
                type:[String,Number],
                require:true
            }
        },
        data(){
            return{
                desc:"",//
                basicList:[],//
                otherList:[],//
                singerid:0,//id
                followers:'',//
                hotSongs:[],//
                albumList:[],//
                singerName:'',//
                mvList:[],//mv
                songTotal:0,//
                albumTotal:0,//
                mvTotal:0,//mv
                simSinger:[],//
                contentNav:['','',''],
                ruleActive:false,
                ruleMsg:'',
                userOperating:['','','','',''],//
                iconList:[
                    'icon-bofang2',
                    'icon-jiatianjiakuangxuanduoxuan-8',
                    'icon-xiazai',
                    'icon-piliangxiugai',
                    'icon-pinglun'
                ],//
                operatingList:[
                    'bofang2',
                    'jia',
                    'xiazai',
                    'fenxiang'
                ],//
                userOperaUrl:[
                    'javascript:;',
                    'javascript:;',
                    'javascript:;',
                    'javascript:;',
                    '#rank-comment',
                ],//
                songOperatingShow:'',//
                showMoreInfo:false,//
            }
        },
        methods:{
          async fetchSingerInfo(){
              let res = await api.SingerIntroduction(this.singermid)
              let resOfFollowers = await api.SingerFollowers(this.singermid)
              let resOfHot = await api.SingerHotSong(this.singermid)
              let resOfAlbum = await api.SingerAlbum(this.singermid)
              let resOfMv = await api.SingerMv(this.singermid)
              let resOfSim = await api.SingerSimilar(this.singermid)
              // debugger
              // console.log(res)
              if (res&&resOfFollowers){
                  this.desc = res.data.data.desc//
                  this.basicList = res.data.data.basic.item//
                  this.otherList = res.data.data.other.item//
                  this.singerid = res.data.data.id//id
                  this.followers = this.numToTenThousand(resOfFollowers.data.response.num)//
                  this.hotSongs = resOfHot.data.data.list//
                  this.songTotal = resOfHot.data.data.total//
                  this.albumList = resOfAlbum.data.data.list//
                  this.albumTotal = resOfAlbum.data.data.total//
                  this.singerName = resOfAlbum.data.data.name//
                  this.mvList = resOfMv.data.data.list//mv
                  this.mvTotal =resOfMv.data.data.total//mv
                  this.simSinger = resOfSim.data.data.list//
              }
          } ,
            //
            numToTenThousand(num){
                if (num < 10000){
                    return num
                }
                let res = num / 10000
                //  toFixed  
                return res.toFixed(1)+''
            },
            //
            OperateChange(index){
                if (index){
                    this.songOperatingShow=index
                }else{
                    this.songOperatingShow=''
                }
            },
            //
            clickMorepanel(){
              this.showMoreInfo = !this.showMoreInfo
            },
        },
        computed:{
            basicInfo(){
                if (this.basicList){
                    let res = ''
                    // debugger
                    this.basicList.forEach(item=>{
                        res += item.key + `: `+item.value+' '
                    })
                    // console.log(res)
                    return res
                }
                return ''
            },
            //
            songTime(){
                return function (interval) {
                    //
                    let m = Math.floor(interval/60)
                    //
                    let s = interval%60
                    //
                    m += ''
                    s += ''
                    //,0
                    m = (m.length==1) ? '0'+m : m
                    s = (s.length==1) ? '0'+s : s
                    return m+':'+s
                }
            },
        },
        created() {
            this.fetchSingerInfo()
        },
        components:{
            SingerInfoShow
        },
        mounted() {
            //,showIndex-1,
            document.addEventListener('click',e=>{
                const contextMenuBox = document.getElementById('singer-more-info')
                if (contextMenuBox){
                    if (!contextMenuBox.contains(e.target)){

                        this.showMoreInfo=false

                    }
                }
            })
        }

    }

</script>
