<template>
    <!---->
  <div class="comment-wrap">
     <!--  v-model content  -->
    <textarea class="comment-input"
              :placeholder="replyName"
              id="textpanel" v-model="content"
              cols="100" rows="4"  maxlength="300"
              @input="contentInput"
    >

    </textarea>
    <!---->
    <span class="remaining-word">
                    <i></i>
                    <i class="rem-word">{{remWord}}</i>
                    <i></i>
    </span>

    <!--  -->
    <div class="opration">
        <!---->
        <!---->
        <!---->
      <div class="comment-submit-wrap">
        <span class="iconfont icon-biaoqing1" @click.stop="showEmojiPanel"></span>
        <a href="javscript:;" class="comment-submit">
          <i>{{ submitButton }}</i>
        </a>
        <a href="javscript:;"
           v-if="isShowDeleteReply"
           class="comment-cancel"
           @click="commentCancel"
        >
           
        </a>
      </div>
        <!---->
        <!---->
        <!--emojiClick,appendEmoji,isShowEmojiPanel-->
        <emoji-panel @emojiClick="appendEmoji" v-if="isShowEmojiPanel" id="emoji-panel"></emoji-panel>
    </div>

  </div>
</template>
<script>
import EmojiPanel from "./children/EmojiPanel";
export default {
  data() {
    return {
      content: "",//
      flag: false,
      isShowEmojiPanel: false,
      comments: [],//
      remWord:300,//
      replyName:"...",
      isShowDeleteReply:false,//
      submitButton:""
    }
  },
  props:{
    name:{
      type: String,
      default: "..."
    }
  },
  components: {
    EmojiPanel
  },
  methods: {
      /**/
    saveComment() {
      /*
         1.content
         2.replace() 
           contentv-model,,content :angry: ,
           replace
              .  \n 
              * 
              ? 
              g 
           emoji
      */
      this.comments.push(this.content.replace(/:.*?:/g, this.emoji)); // ":",emoji<span></span>
      this.content = "";
      this.isShowEmojiPanel = false;
    },

    /**/
    showEmojiPanel() {
      // ->  -> 
      this.isShowEmojiPanel = !this.isShowEmojiPanel;
    },

   //emoji,word,
    emoji(word) {
      // html
      //substring() 
      //   :angry:  angry
      const type = word.substring(1, word.length - 1);
      //class,
      return `<span class="emoji-item-common emoji-${type} emoji-size-small" ></span>`;
    },

    //
    //textEmojiPanel
    appendEmoji(text) {
      //id
      const el = document.getElementById("textpanel");
      //
      this.content = el.value + ":" + text + ":";
    },

    //
    contentInput(){
      let txtVal = this.content.length
      this.remWord = 300 - txtVal
    },

    //
    commentCancel(){
      this.$emit('commentCancel',-1)
    }

  },
  mounted() {
    document.addEventListener('click',e=>{
      const emoji = document.getElementById("emoji-panel")
      if (emoji){
        if (!emoji.contains(e.target)){
            this.isShowEmojiPanel=false
        }
      }
    })
  },
  watch:{
    name: {
      immediate:true,
      handler:function (val){
        if (val!=this.replyName){
          this.replyName="@"+val
          this.isShowDeleteReply=true
          this.submitButton=""
        }

      }
    }
  }
};
</script>

<style lang="scss">
/**/
.comment-wrap {
  left: 0;
  right: 0;
  position: relative;
  //margin: 0 auto;
  //
  //
  .emoji-item-common {
    background: url("../../../../assets/img/emoji_sprite.png");
    display: inline-block;
    &:hover {
      cursor: pointer;
    }
  }
  .emoji-size-small {
    // 
    zoom: 0.3;
  }
  .emoji-size-large {
    zoom: 0.5; // emojipanel
    margin: 4px;
  }

  //
  .comment-input {
    resize: none;
    border-color: #ececec;
    padding: 10px;
    font-size: 19px;
    line-height: 1.5;
    background: #f5f5f5;
    //height: 100px;
    border-radius: 5px;
    &:focus {
      outline: none;
    }
  }
  //
  .remaining-word{
    //border: 1px solid red;
    position: absolute;
    right: 0;
    bottom: 50px;
    text-align:right;
    margin: 10px;
    .rem-word{
      color: #31c27c;
    }
  }

  // ,
  .opration {
    //border: 1px solid red;
    display: flex;
    justify-content: flex-end;
    position: relative;
    //
    .comment-submit-wrap{
      //border: 1px solid red;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin: 10px 0;
      span{
        font-size: 20px;
        color: #31c27c;
        cursor: pointer;
      }
      a{
        //border: 1px solid red;
        background: #31c27c;
        color: white;
        padding: 0px 8px;
        font-size: 14px;
        border-radius: 2px;
        width: 80px;
        line-height: 27px;
        height: 27px;
        text-align: center;
        margin-left: 8px;
        &:hover{
          background: #2caf6f;
        }
      }
      .comment-cancel{
        background: white;
        color: black;
        border: 1px solid #c9c9c9;;
        &:hover{
          background: #ededed;
        }
      }
    }
  }
}
//  
@import "../../../../assets/css/emoji.css";
</style>

