<template>
    <div class="search-content">
        <!---->
        <ul class="search-type-ul">
            <li v-for="(item,index) in typeList" :key="index"
                :class="activeIndex.t==item.t ? 'selectType' : 'normalType'"
                @click="clickType(item.t)"
            >
                <a href="javascript:;">{{item.name}}</a>
            </li>

        </ul>
        <!---->
       <!-- <ul class="userOpera">
            <a :href="`${userOperaUrl[index]}`" v-for="(item,index) in userOperating" :key="index"
               v-show="activeIndex.t==0">
                <span :class="['iconfont ',`${iconList[index]}`]"></span>
                <span>
                        {{item}}
                    </span>
            </a>
        </ul>-->
        <song-list
          v-if="activeIndex.t!=7 &&activeIndex.t!=12"
          :setContentList="contentList"
          :setContentType="activeIndex.t"
        />
        <!---->
        <search-lyrics
          v-if="activeIndex.t==7 "
          :setContentList="contentList"

        />
    <!--    <search-mv
          v-if="activeIndex.t==12 "
          :setContentList="contentList"
        />
-->
    </div>
</template>

<script>
    import SongList from '@/components/main/commont/SongList'
    import SearchLyrics from '@/components/main/search/SearchLyrics'
    import SearchMv from './SearchMv'
    export default {
        props:{
            setSearchWord:{
                type: String
            }
        },
        data(){
            return{
                typeList:[
                    {
                        name:'',
                        t:0
                    },
                    {
                        name:'',
                        t:8
                    },
                    // {
                    //     name:'MV',
                    //     t:12
                    // },
                    {
                        name:'',
                        t:2
                    },
                    {
                        name:'',
                        t:7
                    },






                ],//
                userOperating:['','','',''],//
                userOperaUrl:[
                    'javascript:;',
                    'javascript:;',
                    'javascript:;',
                    'javascript:;',
                ],//
                iconList:[
                    'icon-bofang2',
                    'icon-jiatianjiakuangxuanduoxuan-8',
                    'icon-xiazai',
                    'icon-piliangxiugai',
                ],//
                activeIndex:{
                    key:'',//
                    pageNo:1,//
                    pageSize:20,//
                    t:0//
                },//
                contentList:[],//
                songOperatingShow:'',//
                operatingList:[
                    'bofang2',
                    'jia',
                    'xiazai',
                    'fenxiang'
                ],//
                labelList:[],//
                labelListOfSong:[
                    '',
                    '',
                    '',
                    ''
                ],//-
                labelListOfAlbum:[
                    '',
                    '',
                    ''
                ],//-
                labelListOfSongList:[
                    '',
                    '',
                    ''
                ],//-

            }
        },
        methods:{
            async fetchSearchApi(params){
                // debugger
              let url = '/api/search'
                if (!params){
                    params = this.activeIndex
                }
                const res = await this.$http.get(url,{params})
                this.contentList = res.data.data.list
                // if (res.data.data){
                //     this.contentList = res.data.data.list
                // }
                // debugger
                // console.log(this.contentList)

            },
            //
            clickType(t){
                // debugger
             /*   if (t!=this.activeIndex.t){

                }*/
                this.activeIndex.t = t
                this.fetchSearchApi()
            },
            //
            OperateChange(index){
                if (index){
                    this.songOperatingShow=index
                }else{
                    this.songOperatingShow=''
                }
            },
            setFormatForSongList(){
                // debugger
                // console.log(this.activeIndex)
                // console.log(this.$route.query.inputSearch);
                this.activeIndex.key=this.$route.query.inputSearch
            }

        },
        computed:{
            //
            songTime(){
                return function (interval) {
                    //
                    let m = Math.floor(interval/60)
                    //
                    let s = interval%60
                    //
                    m += ''
                    s += ''
                    //,0
                    m = (m.length==1) ? '0'+m : m
                    s = (s.length==1) ? '0'+s : s
                    return m+':'+s
                }
            },
        },
        watch:{
            //
            $route:{
                handler:function (val,oldVal) {
                    if (val.path=='/search'){
                        // debugger
                        this.activeIndex.key = val.query.inputSearch
                        this.activeIndex.t=0
                        this.fetchSearchApi(this.activeIndex)
                        // console.log(this.activeIndex)
                    }
                },
                //
                deep:true
            },



        },
        created() {
            this.setFormatForSongList()
            this.fetchSearchApi()
        },

        components:{
            SongList,
            SearchLyrics,
            SearchMv
        }
    }
</script>
