<template>
    <ul class="paging">
        <!--
            prev
            paging-item li
            paging-item-change 
            v-show="index != 1" 1,,1
            @click="prev"  this.index-1 
         -->
        <li
            class="paging-item paging-item-change"
            v-show="index != 1"
            @click="prev">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-jiantou3"></use>
            </svg>
        </li>

        <!--
            first  1
            v-show="index > 3" 1-3,,1-5
                               4,1,11

        -->
        <li
            class="paging-item"
            v-show="index > 3"
            @click="first">1</li>
        <!--
            
            showPrevMore false,pagers,
        -->
        <li
            class="paging-item"
            v-if="showPrevMore">...</li>
        <!--
            
            paging-item--current ,pagerpagers,indexpagertrue
            @click="go(pager)" ,
        -->
        <li
            :class="['paging-item', {'paging-item--current' : index === pager}]"
            v-for="pager in pagers"
            @click="go(pager)">{{ pager }}</li>
        <!---->
        <li
            class="paging-item"
            v-if="showNextMore">...</li>

        <!--
            last 
            index<total-2298-2=296,
            295,,298
            ,298,
        -->
        <li
            class="paging-item"
            v-show="index<pageNum-2"
            @click="last">{{pageNum}}</li>

        <!--
            next 
            v-show="index != total" ,,
        -->
        <li
            class="paging-item  paging-item-change"
            v-show="index != pageNum"
            @click="next">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-jiantouyouxi"></use>
            </svg>
        </li>
    </ul>
</template>

<script>


export default {
    //props
    props : {

        //..., 
        perPages : {
            type : Number,
            default : 5
        },

        //
        pageIndex : {
            type : Number,
            default : 1
        },

        //
        pageSize : {
            type : Number,
            default : 20
        },

        //
        total : {
            type : Number,
            default : 1
        },

        //
        totalAll:{
            type: Number,
            default : 20
        }

    },
    data () {
        return {
            index : this.pageIndex, //
            limit : this.pageSize, //
            pageNum : this.total || 1, //
            showPrevMore : false,
            showNextMore : false
        }
    },
    methods : {
        /**/
        prev(){
            if (this.index > 1) {
                this.go(this.index - 1)
            }
        },
        /**/
        next(){
            if (this.index < this.pageNum) {
              this.go(this.index + 1)
            }
        },
        /**/
        first(){
            if (this.index !== 1) {
              this.go(1)
            }
        },
        /**/
        last(){
            if (this.index != this.pageNum) {
                this.go(this.pageNum)
            }
        },
        /**/
        go (page) {

            /*
                go,
                this.index,page,
            */
            if (this.index !== page) {
                //page
                this.index = page
                //$emit,change,this.index
                //@changethis.index
                this.$emit('change', this.index)
            }
        }
    },
    computed : {

          /*      //
                pages(){
                     return Math.ceil(this.size / this.limit)
                },*/

        //count
        pagers () {
            /*array*/
            const array = []
            /* 5*/
            const perPages = this.perPages
            /* 298*/
            const pageCount = this.pageNum
            /**/
            let current = this.index
            /*,5,2*/
            const _offset = (perPages - 1) / 2

            /*
                 start :-
                 end: +
            */
            const offset = {
                start : current - _offset,
                end   : current + _offset
            }

            //-1, 3
            /*
                1,offset.star-1,offset.end3
                offset.end  = 3+(1- (-1))=5
                2,offset.star0,offset.end4
                offset.end  = 4+(1- 0)=5
                offset.end = 4,1,2,4
            */
            if (offset.start < 1) {
                // offset.end = offset.end + (1 - offset.start)
                offset.start = 1
                offset.end = 4
            }
            /*
                
                297
                offset.end=297+2=299>298
                offset.start=297-2=295
                offset.start = 295-(299-298)=294

                offset.start = offset.start - (offset.end - pageCount)
                ,,start

                offset.start = pageCount-3 start298-3=295,295298

            */
            if (offset.end > pageCount) {
                offset.start = offset.start - (offset.end - pageCount)
                offset.end = pageCount
                offset.start = pageCount-3
            }
            //offset.start11
            if (offset.start < 1) {
                offset.start = 1
            }
            /*
                ,offset.start 1,,showPrevMoretrue,
                        offset.end ,,showNextMoretrue,
             */
            this.showPrevMore = (offset.start > 2)
            this.showNextMore = (offset.end < pageCount-1)

            /* offsetstartend */
            for (let i = offset.start; i <= offset.end; i++) {
                 array.push(i)
            }
            /**/
            return array
        }
    },

    // watchVue
    watch : {
        /*
           pageIndexprops,
           watch,,,
           this.index,,1
           total
        */
        //
        pageIndex(val) {
             this.index = val || 1
        },
        //
        pageSize(val) {
             this.limit = val || 20
        },
        //
        total(val) {
              this.pageNum = val || 1
        },
        //
        totalAll(val) {
                //,
                // this.pageNum = Math.ceil(val /this.limit) || 1
                //api,api,floor

                this.pageNum =  val<this.limit ? 1 : Math.floor(val /this.limit)

        }
    }
}
</script>
