import axios from 'axios'
const api = {
    //
    songInfo(songId){
        return axios.get('/api/song',{
            params:{
                songmid:songId
            }
        })
    },
    //
    songLyric(songId){
        return axios.get('/api/lyric',{
            params:{
                songmid:songId
            }
        })
    },
    //
    songPlayer(songId){
        return axios.get('/api/song/urls',{
            params:{
                id:songId
            }
        })
    },
    //
    songList(content_id){
        return axios.get('/api/songlist',{
            params:{
                id:content_id
            }
        })
    },
    //
    albumList(albummid){
        return axios.get('/api/album/songs',{
            params:{
                albummid:albummid
            }
        })
    },
    // 
    addSongList(mid,dirid){
        debugger
        let type = Object.prototype.toString.call(mid)
        let mids = ''
        if (type==='[object Array]'){

            mid.forEach(item=>{
                mids += item + ','
            })
        }

        let res = axios.get('/api/songlist/add',{
            params:{
                mid:mid,
                dirid:dirid
            }
        })

        return res
        /*
        *
        * {"result":301,"errMsg":""}
        * */
    },
    //
    downSongs(mid,type){
        return axios.get('/api/song/url',{
            params:{
                id:mid,
                type:type || 128,
                isRedirect:0
            }
        })

    },
    //
    //
    recommendForU(){
        return axios.get('/api/recommend/playlist/u')
    },
    //
    recommendPlayList(id,pageNo,pageSize){
        return axios.get('/api/recommend/playlist',{
            params:{
                id,
                pageNo:pageNo || 1,
                pageSize:pageSize || 20
            }
        })
    },
    //
    newSongStarting(type){
        return axios.get('/api/new/songs',{
            params:{
                type //type:  0 // 0:  12345
            }
        })
    },
    //
    newRecordStarting(type,num){
        return axios.get('/api/new/album',{
            params:{
                type, //type:  1 // 123456
                num:num || 11,// 10
            }
        })
    },
    //
    rankList(id,pageSize){
        return axios.get('/api/top',{
            params:{
                id: id,//id 62 ,26 ,27 ,4 ,67 
                pageSize: pageSize || 100
            }
        })
    },
    //
    RelatedSongList(id){
        return axios.get('/api/song/playlist',{
            params:{
                id, //songid
            }
        })
    },
    //MV
    RelatedMV(id){
        return axios.get('/api/song/mv',{
            params:{
                id, //songid
            }
        })
    },
    //
    RelatedComment(id,pageNo,type){
        return axios.get('/api/comment',{
            params:{
                id, //songid
                pageNo:pageNo||1,//
                type:type||1,//  0 // 01
            }
        })
    },
    //mv
    MvRecommend(type){
        return axios.get('/api/new/mv',{
            params:{
                type:type||0, //type:  0, 0:  12345
            }
        })
    },
    //mv
    MvPlaying(vid){
        return axios.get('/api/mv/url',{
            params:{
                id:vid||0, //id:  vid , ,
            }
        })
    },
    //mv
    MvInfo(vid){
        return axios.get('/api/mv',{
            params:{
                id:vid||0, //id:  vid , ,
            }
        })
    },
    //
    SingerIntroduction(singermid){
        return axios.get('/api/singer/desc',{
            params:{
                singermid: singermid
            }
        })
    },
    //
    SingerFollowers(singermid){
        return axios.get('http://www.cqxiong.ml:3200/getSingerStarNum',{
            params:{
                singermid: singermid
            }
        })
    },
    //
    SingerHotSong(singermid){
        return axios.get('/api/singer/songs',{
            params:{
                singermid: singermid
            }
        })
    },
    //
    SingerAlbum(singermid){
        return axios.get('/api/singer/album',{
            params:{
                singermid: singermid
            }
        })
    },
    //mv
    SingerMv(singermid){
        return axios.get('/api/singer/mv',{
            params:{
                singermid: singermid
            }
        })
    },
    //
    SingerSimilar(singermid){
        return axios.get('/api/singer/sim',{
            params:{
                singermid: singermid
            }
        })
    }
}
export default api
