<template>
    <div >
        <div class="rotation-title"
            :class="`title-${index}`">
            <!---->
            <div class="song-list">
                <!---->
                <div class="song-list-title-wrap">
                    <!---->
                    <div class="song-list-title">
                        <img :src="require('@/assets/img/index_tit@2x.png')" alt="" class="title-img">
                    </div>
                </div>
                <!---->
                <div class="song-wrap">
                    <!---->
                    <ul class="song-list-ul">
                        <!--@click,navClick,navClick-->
                        <!--isAcive-->
                        <li
                            v-for="(item,n) of List[index]"
                            :key="n"
                            :class="['select']"
                            @click="navClick(item.path,index,n)"
                        >
                            <i :class="[`item-name-${n}`,{'nav-active':isClickFlag(n)}]"
                        >
                                {{item.name}}
                            </i>
                            <!---->
                            <div v-if="isRank(item)" class="isrank">
                                <div v-for((v,i) in 3)>
                                        <i class="singerList-rank">{{this.songList[i].rank}}</i>
                                        <i class="singerList-title">{{this.songList[i].title}}</i>
                                        <i class="singerList-singerName">{{this.songList[i].singerName}}</i>
                                </div>
                            </div>
                        </li>
                        <!---->
                        <div class="playAll" v-if="index==1">
                            <a href="javascript:;" class="playAllWrap">

                                <div class="playInfoWrap">
                                    <svg class="icon bofang1" aria-hidden="true">
                                        <use xlink:href="#icon-bofang1"></use>
                                    </svg>
                                    <i>
                                        
                                    </i>
                                </div>
                            </a>
                        </div>
                    </ul>
                </div>
            </div>
        </div>

        <!--rotation-api
            :indexindex,propsindex
            indexhomepagerotation-title,rotation-titlerotation-api
            clickPathapi
            tilteref(api),
            this.$refs.api.
            this.$refs.api. 
        -->
        <keep-alive>
            <rotation-api
              :index="index"
              ref="api"
            />
        </keep-alive>

    </div>
</template>

<script>
import RotationShow from "@/components/main/commont/rotation/RotationShow";
import RotationApi from "@/components/main/commont/rotation/RotationApi";
import api from "@/api";
export default {
    /*
    * rotation-tilehomepage
    * props
    * index,index,index,homepage
    * */
    props:{

        index:{
            type: Number,
            required: true,
        },

    },
    data(){
        return{
            List:[
                //
                [
                        {path: 'u',name:''},
                        {path: '3317',name:''},
                        {path: '71',name:''},
                        {path: '3056',name:''},
                        {path: '59',name:''},
                        {path: '64',name:'KTV'},
                    ],
                //
                [
                        {path: '0',name:''},
                        {path: '1',name:''},
                        {path: '2',name:''},
                        {path: '3',name:''},
                        {path: '4',name:''},
                        {path: '5',name:''},
                     ],
                // 
                //
                [
                    {path: '1',name:''},
                    {path: '2',name:''},
                    {path: '3',name:''},
                    {path: '4',name:''},
                    {path: '5',name:''},
                    {path: '6',name:''},
                ],
                //
                [
                    {path: '62',name:''},
                    {path: '26',name:''},
                    {path: '27',name:''},
                    {path: '4',name:''},
                    {path: '67',name:''},
                ],
                //MV
                [
                    {path: '0',name:''},
                    {path: '1',name:''},
                    {path: '2',name:''},
                    {path: '3',name:''},
                    {path: '4',name:''},
                    {path: '5',name:''},
                ],
            ],//
            songList:null,//
            clickFlag:0,//
            clickPath:[],//
        }
    },

    methods:{
        /*navClick,this.$emit,
        * nav-click
        * path
        * nav-click,path,homepage@
        * */
        navClick(path,index,clickIndex){
            // 
            this.$emit('nav-click',path)
            //clickPath
            // debugger
            // this.clickPath=path
            this.$refs.api.clickPath(path,index)
            this.clickFlag=clickIndex
         },

        isClickFlag(flag){
           return  flag==this.clickFlag ? true :false
        },

        isRank(res){
            // debugger
            // console.log(this.apiGetList)
            // console.log(res)
            if (!res.song){
                return false
            }
            // debugger
            this.songList = res.song.map(item=>({
                rank:item.rank,
                singerName:item.singerName,
                songId:item.songId,
                title:item.title,
            }))
            return true
        }

    },
    components:{
        RotationShow,
        RotationApi
    },
    mounted() {

    },
    created() {
        // this.fetchRecommendSwiper()
    }

}

</script>



