import api from "@/api";

function timeFormat(val){
    // debugger
    let min = Math.floor(val/60)
    let s = Math.floor(val%60)
    min = min<10 ? '0'+min : min
    s = s<10 ? '0'+s : s
    return min + ':' + s

}
// 
function timeConversion(UnixTime){
        // debugger
        let unixTimestamp = new Date( UnixTime * 1000)
        let Y = unixTimestamp.getFullYear()
        let M = ((unixTimestamp.getMonth() + 1) >= 10 ? (unixTimestamp.getMonth() + 1) : '0' + (unixTimestamp.getMonth() + 1))
        let D = (unixTimestamp.getDate() > 10 ? unixTimestamp.getDate() : '0' + unixTimestamp.getDate())
        let H = (unixTimestamp.getHours() > 10 ? unixTimestamp.getHours() : '0' + unixTimestamp.getHours())
        let m = (unixTimestamp.getMinutes() > 10 ? unixTimestamp.getMinutes() : '0' + unixTimestamp.getMinutes())
        return Y + '-' + M+'-'+D
}
// 
function formatLyric(lyric) {
    let lyrics = lyric.split("\n")
    //
    let lyrArr = {}
    //* ,
    let reg = /\[\d*:\d*\.\d*]/g
    for (let i = 0; i < lyrics.length; i++) {
        let timerRegExpArr = lyrics[i].match(reg)
        if (!timerRegExpArr) continue
        let t = timerRegExpArr[0] //,
        //
        let min = Number(t.match(/\[\d*/).toString().slice(1))
        //
        let second = Number(t.match(/:\d*/).toString().slice(1))
        //
        let content = lyrics[i].replace(timerRegExpArr,"")
        //,
        if (content){
            //
            let time = min*60+second
            //
            lyrArr[time] = content
        }

    }
    // debugger
    return lyrArr
}
// 
function formatLyricHtml(lyric) {
    let lyrics = lyric.split("\n")
    //
    let lyrArr = []
    //* ,
    let reg = /\[\d*:\d*\.\d*]/g
    for (let i = 0; i < lyrics.length; i++) {
        let timerRegExpArr = lyrics[i].match(reg)
        if (!timerRegExpArr) continue
        let t = timerRegExpArr[0] //,
        //
         Number(t.match(/\[\d*/).toString().slice(1))
        //
         Number(t.match(/:\d*/).toString().slice(1))
        //
         lyrArr += lyrics[i].replace(timerRegExpArr,"") + '<br />'
    }
    // debugger
    return lyrArr
}

