export const queryRecommendList = (state,payload) =>{
    // debugger
    let {
        id,//id
        list,//
    } = payload
    //id

    state.curRecId = id
    //list,
    if (list){
        list.forEach(item=>{
            item.imgUrl=item.cover_url_small||item.cover_url_medium
            item.content_id=item.tid
            item.listen_num=item.access_num
        })

        state.recommendList.push({
            ...list,
            id,
        })

    }
}

