export const queryDataSongInfo = (state, payload)=>{
    // debugger
    let {
        songId,//id
        songid,//id
        dataOfSongInfo,//
        dataOfRelatedSongList,//
        dataOfRelatedMV,//mv
        dataOfRelatedComment,//
        dataOfSongLyrics,//
    }=payload
    // state.cur = songId || content_id
    if (songId){
        state.curSongInfoId = songId
    }
    // 
    if (songId&&dataOfSongInfo){
        state.songInfoPages.push({
            songId,//id
            songid,//id
            imgUrl:`https://y.gtimg.cn/music/photo_new/T002R300x300M000${dataOfSongInfo.data.track_info.album.pmid}.jpg` ,
            title: dataOfSongInfo.data.track_info.title,//
            singer: dataOfSongInfo.data.track_info.singer,//
            album: dataOfSongInfo.data.track_info.album.title,//
            genre:dataOfSongInfo.data.info.genre||null,//
            pub_time:dataOfSongInfo.data.info.pub_time.content[0].value||null,//
            lan:dataOfSongInfo.data.info.lan.content[0].value||null,//
            company:dataOfSongInfo.data.info.company.content[0].value||null,//
            lyric:formatLyricHtml(dataOfSongLyrics),//
            commend:dataOfRelatedComment,//
            RelatedSongList:dataOfRelatedSongList,//
            RelatedMV:dataOfRelatedMV,//mv
        })
    }
    //
    else if (dataOfRelatedComment){
        // debugger
        let index = state.songInfoPages.findIndex(item=>item.songId==state.curSongInfoId)
        state.songInfoPages[index].commend=dataOfRelatedComment
    }

}

