<template>
    <div class="search-title">


        <search-bar
            @getHotSearch="getHotSearch"
            :setSearchWord="clickWord"
        />
        <div class="hotSearch">
            <i class="hotSearch-title">:</i>
            <ul class="hotSearch-ul">
                <li v-for="item in hotSearchList" @click="clickHot(item.name)">
                    {{item.name}}
                </li>
            </ul>
        </div>

    </div>
</template>
<script>
    import SearchBar from './SearchBar'
    export default {
        components:{
            SearchBar
        },
        data(){
            return{
                hotSearchList:[],//
                clickWord:'',//
            }
        },
        methods:{
            //
            getHotSearch(val){
                this.hotSearchList=val
            },
            clickHot(val){
                this.clickWord=val
            }
        }

    }
</script>
