<template>
    <div class="picture-show"  :style="{'--width':imgWidth,'--height':imgHeight}">
        <div class="img-wrap ">
           <!-- <img  class="img-info  checked-style"
                  :src="imgUrl"
            >-->
            <img  class="img-info  checked-style"
                  :src="imgUrl"
            >

            <nav>

                <router-link :to="`/${routePath}/`+songId"   class="img_cover_wrap">
                    <img src="../../../../public/img/cover_play@2x.png" alt="" class="img_cover_button">
                </router-link>

            </nav>
        </div>
<!--        {{songId}}-->
    </div>
</template>

<script>
    import {mapGetters} from "vuex";

    export default {
        props:{
            //
            imgUrl:{
                type: String,
                require: true
            },
            //id
            songId:{
                type: [String,Number],
                require: true
            },
            //mv
            isMv:{
                type: Boolean,
                require: true
            }
        },
        data(){
            return{
                //
                imgWidth:0,
                imgHeight:0,
            }
        },
        methods:{
          getImgInfo(){
              // debugger
              //
              let img = new Image()
              //src
              img.src = this.imgUrl
              //
              this.imgWidth=img.width+'px'
              this.imgHeight=img.height+'px'
          },
        },
        computed:{
            routePath(){
                if (!this.isMv){
                    return 'player'
                }else {
                    return 'mvplayer'
                }
            }


        },
        watch:{
         /*   imgUrl(val){
                this.url = val
            }*/

        },
        created() {
            this.getImgInfo()


        },
        mounted() {
        }
    }
</script>

<style lang="scss">
    .picture-show{
        //border: 1px solid red;
        position: relative;
        width: var(--width);
        height: var(--height);
        overflow: hidden;
        //
        .img-wrap{
            transition: transform 1s;
            // 
            .img-info{
                /* width: 224px;
                 height: 224px;*/
                width: 100%;
                height: 100%;
                transition: transform 1s;
            }
            //
            .checked-style{
                //a
                cursor:pointer;
            }

            //
            .img_cover_wrap{
                position: absolute;
                top: 0;
                left: 0;
                //
                background-image: radial-gradient( rgba(0,0,0,0.1),  rgba(0,0,0,0.11));
                /*  */
                background-color: rgba(0,0,0,0.1);
                font-size: 60px;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: transform 0.4s,opacity 1s;
                //
                .img_cover_button{
                    //border: 1px solid green;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    color: black;
                    width: 80px;
                    height: 80px;
                    transform: scale(0.6,0.6);
                    transition: transform 0.8s,opacity 0.8s;
                }
            }
            //opacity,
            &:hover .img_cover_wrap{
                opacity: 1;
            }
            &:hover .img_cover_button{
                opacity: 1;
                transform: scale(0.9,0.9);
            }

        }
        //
        &:hover img{
            transform: scale(1.1);
        }

    }
</style>
